import React, { Component } from 'react' 
import { Redirect } from "react-router-dom"
import { updateToken } from './TokenStore';

class GetToken extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            activeToken: false
        }
    }

    // nr sidan laddas upp fr frsta gngen nr vi hller p att logga in, s vill vi spara sjlva token i vr TokenStore.js
    // Samtidigt s gr vi s att vi redirectar till /main
    componentDidMount() {
        let fullHash = window.location.hash;
        let hash = fullHash.match(/[A-Za-z\d\.\-_]{3,}/g);
        this.setState({ activeToken: true });
        updateToken(hash[1]);
    }

    render() {
        const { activeToken } = this.state;

        if (activeToken) return <Redirect to="/main" />

        return (
            <>
            </>
        )
    }
}

export default GetToken
